:root {
	--base04: #002129;
	--base03: #002b36;
	--base02: #073642;
	--base01: #586e75;
	--base00: #657b83;
	--base0: #839496;
	--base1: #93a1a1;
	--base2: #eee8d5;
	--base3: #fdf6e3;
	--yellow: #b58900;
	--orange: #cb4b16;
	--red: #dc322f;
	--magenta: #d33682;
	--violet: #6c71c4;
	--blue: #268bd2;
	--cyan: #2aa198;
	--green: #859900;
}

body {
	background: var(--base03);
	color: var(--base0);
	padding: .25em;
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
	color: var(--blue);
}

form.token input[type="text"] {
	background: var(--base02);
	color: var(--base0);
	border: none;
	padding: .75em;
	margin-top: .75em;
	min-width: 20em;
	border-radius: .25em;
}

form.token input[type="submit"] {
	background: var(--cyan);
	color: var(--base02);
	border: none;
	padding: .5em 1.5em .5em 1.5em;
	margin-top: 1em;
	border-radius: .25em;
}

form.token input[type="submit"]:active {
	opacity: 0.5;
}

div.symbols-editor {
	margin-bottom: .5em;
}


div.symbols-editor input[type="text"] {
	background: var(--base04);
	color: var(--base0);
	padding: 1em;
	border: none;
	border-radius: .25em;
	width: calc(100% - 2em);
}

div.App div.widget-list {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	gap: .5em;
}

@keyframes blink-green {
	0% {
		background: var(--base04);
	}
	50% {
		background: #5b6809;
	}
	100% {
		background: var(--base04);
	}
}

@keyframes blink-red {
	0% {
		background: var(--base04);
	}
	50% {
		background: #821c1a;
	}
	100% {
		background: var(--base04);
	}
}

div.widget {
	padding: 1em;
	background: var(--base04);
	border-radius: .25em;
}

div.blink-green {
	animation: blink-green linear .5s 1 forwards;
}

div.blink-red {
	animation: blink-red linear .5s 1 forwards;
}

div.widget div.progress-bar {
	border-radius: .1em;

	opacity: 0.5;

	height: .2em;
	width: 0;

	transition: .5s ease;

	background: var(--base00);

	margin-bottom: .5em;
}

div.widget h3 {
	color: var(--base0);
	margin: 0;
}

div.widget p {
	color: var(--base00);
	margin: 0;
	margin-top: .5em;
}

div.widget p.green {
	color: var(--green);
}

div.widget p.red {
	color: var(--red);
}

div.widget div.profile {
	float: left;
}

div.widget div.quote {
	float: right;
	text-align: right;
}

div.widget div.flex-container {
	display: flex;
	flex-flow: row nowrap;
}

div.widget div.grid-container {
	display: grid;
	grid-template-columns: auto auto;
}


div.widget div.chart {
	margin-top: .75em;
}

div.widget div.quote-updated-date {
	font-size: 8pt;
	margin-top: 2em;
	color: var(--base00);
}

div.widget div.quote-updated-date > div {
	margin-top: .4em;
}